.header{
    width: 100%;
    display: flex;
    flex-flow: column;
}
.header p{
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
}
.header ul{
    height: 86px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;
    border-bottom: 1px solid #333;
}
.header ul li{
    font-size: 20px;
    font-weight: 700;
}
.header button{
    width: 50px;
    height: 30px;
    margin: 20px auto;
    cursor: pointer;
}